.App {
  font-family: sans-serif;
  text-align: center;
  height: 100vh;
  padding-top: 100px;
  background-color: #9f9f9f;
  background-image: url("https://www.transparenttextures.com/patterns/diagmonds-light.png");
}
h1 {
  color: #000;
  font-size: 50px;
}
div StarBorderIcon {
  color: #fff;
}
button {
  background-color: #fff;
  font-weight: bold;
  font-size: 16px;
  padding: 5px 10px;
  margin: 3px;
  height: 30px;
  width: 100px;
  border-radius: 4px;
  border: 4px inset #f9f9f9;
  box-shadow: 0 10px 10px -4px #5f5f5f;
  transition: 0.3s ease;
}
button:hover {
  border: 4px outset #f9f9f9;
  cursor: pointer;
  transform: scale(1.05);
  box-shadow: 0 14px 14px -8px #5f5f5f;
}
button:focus {
  outline: 0;
}
.timer {
  font-size: 100px;
}

.hours,
.mins,
.secs {
  color: #000;
  font-size: 100px;
  transition: 0.5s ease;
  background-color: #fff;
  padding: 0 10px;
  border: 3px inset #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 10px 10px -4px #5f5f5f;
}
.title {
  text-shadow: 0 2px 6px #7f7f7f;
}
.star {
  width: 50px;
  height: 50px;
  margin: 5px;
  animation: fade 1s infinite;
  transition: 0.5s ease-in;
}
